.container {
  min-height: 100vh;
}

/* Header */
.header {
  border-bottom: 1px solid color-mix(in oklch, var(--border) 40%, transparent);
  background-color: color-mix(in oklch, var(--background) 95%, transparent);
  backdrop-filter: blur(8px);
  position: sticky;
  top: 0;
  z-index: 50;
}

.headerContent {
  max-width: 1280px;
  margin: 0 auto;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.logoIcon {
  width: 2rem;
  height: 2rem;
  background-color: var(--primary);
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-foreground);
}

.logoText {
  font-weight: 700;
  font-size: 1.25rem;
}

.nav {
  display: none;
  align-items: center;
  gap: 1.5rem;
}

@media (min-width: 768px) {
  .nav {
    display: flex;
  }
}

.navLink {
  font-size: 0.875rem;
  font-weight: 500;
  transition: color 0.2s;
}

.navLink:hover {
  color: var(--primary);
}

/* Hero Section */
.hero {
  position: relative;
  overflow: hidden;
  padding: 5rem 1rem;
}

@media (min-width: 768px) {
  .hero {
    padding: 8rem 1rem;
  }
}

.heroBackground {
  position: absolute;
  inset: 0;
  background: linear-gradient(
    135deg,
    color-mix(in oklch, var(--primary) 5%, transparent),
    color-mix(in oklch, var(--accent) 5%, transparent),
    var(--background)
  );
}

.heroContent {
  max-width: 1280px;
  margin: 0 auto;
  position: relative;
}

.heroText {
  max-width: 56rem;
  margin: 0 auto 4rem;
  text-align: center;
}

.badge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background-color: color-mix(in oklch, var(--primary) 10%, transparent);
  color: var(--primary);
  padding: 0.5rem 1rem;
  border-radius: 9999px;
  font-size: 0.875rem;
  font-weight: 500;
  margin-bottom: 1.5rem;
}

.heroTitle {
  font-size: 2.25rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  text-wrap: balance;
}

@media (min-width: 768px) {
  .heroTitle {
    font-size: 3.75rem;
  }
}

@media (min-width: 1024px) {
  .heroTitle {
    font-size: 4.5rem;
  }
}

.highlight {
  color: var(--primary);
}

.heroSubtitle {
  font-size: 1.25rem;
  color: var(--muted-foreground);
  margin-bottom: 2rem;
  text-wrap: pretty;
}

@media (min-width: 768px) {
  .heroSubtitle {
    font-size: 1.5rem;
  }
}

.features {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 3rem;
}

.feature {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.875rem;
}

.checkIcon {
  color: var(--primary);
}

.formWrapper {
  max-width: 42rem;
  margin: 0 auto;
}

/* Benefits Section */
.benefits {
  padding: 5rem 1rem;
  background-color: color-mix(in oklch, var(--muted) 30%, transparent);
}

.benefitsContent {
  max-width: 1280px;
  margin: 0 auto;
}

.sectionHeader {
  text-align: center;
  margin-bottom: 4rem;
}

.sectionTitle {
  font-size: 1.875rem;
  font-weight: 700;
  margin-bottom: 1rem;
  text-wrap: balance;
}

@media (min-width: 768px) {
  .sectionTitle {
    font-size: 3rem;
  }
}

.sectionDescription {
  font-size: 1.125rem;
  color: var(--muted-foreground);
  max-width: 42rem;
  margin: 0 auto;
  text-wrap: pretty;
}

.benefitsGrid {
  display: grid;
  gap: 1.5rem;
}

@media (min-width: 768px) {
  .benefitsGrid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .benefitsGrid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.benefitCard {
  background-color: var(--card);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: 1.5rem;
  transition: box-shadow 0.3s;
}

.benefitCard:hover {
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1);
}

.benefitIcon {
  width: 3rem;
  height: 3rem;
  background-color: color-mix(in oklch, var(--primary) 10%, transparent);
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
  color: var(--primary);
}

.benefitTitle {
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.benefitDescription {
  color: var(--muted-foreground);
}

/* Pricing Section */
.pricing {
  padding: 5rem 1rem;
}

.pricingContent {
  max-width: 1280px;
  margin: 0 auto;
}

/* CTA Section */
.cta {
  padding: 5rem 1rem;
  background-color: var(--primary);
  color: var(--primary-foreground);
  text-align: center;
}

.ctaContent {
  max-width: 1280px;
  margin: 0 auto;
}

.ctaTitle {
  font-size: 1.875rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  text-wrap: balance;
}

@media (min-width: 768px) {
  .ctaTitle {
    font-size: 3rem;
  }
}

.ctaDescription {
  font-size: 1.25rem;
  margin-bottom: 2rem;
  opacity: 0.9;
  max-width: 42rem;
  margin-left: auto;
  margin-right: auto;
  text-wrap: pretty;
}

/* Footer */
.footer {
  border-top: 1px solid var(--border);
  padding: 3rem 1rem;
}

.footerContent {
  max-width: 1280px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

@media (min-width: 768px) {
  .footerContent {
    flex-direction: row;
  }
}

.footerText {
  font-size: 0.875rem;
  color: var(--muted-foreground);
}
