
/* Removendo imports do Tailwind e criando sistema CSS puro */
/* Reset básico */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*{
  scroll-behavior: smooth;
}

html {
  background: none;
}

/* Variáveis CSS para o sistema de design */
:root {
  /* Fontes */
  --font-work-sans: "Work Sans", sans-serif;
  --font-open-sans: "Open Sans", sans-serif;

  /* Paleta de cores corporativa */
  --primary: #007acc;
  --primary-blue: #003366;
  --secondary-blue: #007acc;
  --light-blue: #e6f3ff;
  --dark-gray: #2c3e50;
  --medium-gray: #64748b;
  --light-gray: #f8fafc;
  --white: #ffffff;
  --black: #000000;

  /* Sistema de espaçamento consistente */
  --spacing-xs: 0.5rem; /* 8px */
  --spacing-sm: 1rem; /* 16px */
  --spacing-md: 1.5rem; /* 24px */
  --spacing-lg: 2rem; /* 32px */
  --spacing-xl: 3rem; /* 48px */
  --spacing-2xl: 4rem; /* 64px */
  --spacing-3xl: 6rem; /* 96px */
  --spacing-4xl: 8rem; /* 128px */

  /* Sistema de bordas e sombras */
  --border-radius: 8px;
  --border-radius-lg: 12px;
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1);

  /* Transições */
  --transition-fast: 0.2s ease;
  --transition-normal: 0.3s ease;
  --transition-slow: 0.5s ease;

  /* Mantendo variáveis shadcn/ui para compatibilidade */
  --background: #ffffff;
  --foreground: #000000;
  --card: #ffffff;
  --card-foreground: #000000;
  --popover: #ffffff;
  --popover-foreground: #000000;
  
  --primary-foreground: #f3f3f3;
  --secondary: #e8e8e8;
  --secondary-foreground: #020202;
  --muted: #e8e8e8;
  --muted-foreground: #2b2b2b;
  --accent: #e8e8e8;
  --accent-foreground: #020202;
  --destructive: #cc0000;
  --destructive-foreground: #cc0000;
  --border: #c7c7c7;
  --input: #c7c7c7;
  --ring: #5a5a5a;
  --chart-1: #e81100;
  --chart-2: #004e3f;
  --chart-3: #011320;
  --chart-4: #ff7c00;
  --chart-5: #fb5100;
  --sidebar: #f3f3f3;
  --sidebar-foreground: #000000;
  --sidebar-primary: #020202;
  --sidebar-primary-foreground: #f3f3f3;
  --sidebar-accent: #e8e8e8;
  --sidebar-accent-foreground: #020202;
  --sidebar-border: #c7c7c7;
  --sidebar-ring: #5a5a5a;
  --radius: 0.625rem;

  /* optional: --font-sans, --font-serif, --font-mono if they are applied in the layout.tsx */
  --color-background: var(--background);
  --color-foreground: var(--foreground);
  --color-card: var(--card);
  --color-card-foreground: var(--card-foreground);
  --color-popover: var(--popover);
  --color-popover-foreground: var(--popover-foreground);
  --color-primary: var(--primary);
  --color-primary-foreground: var(--primary-foreground);
  --color-secondary: var(--secondary);
  --color-secondary-foreground: var(--secondary-foreground);
  --color-muted: var(--muted);
  --color-muted-foreground: var(--muted-foreground);
  --color-accent: var(--accent);
  --color-accent-foreground: var(--accent-foreground);
  --color-destructive: var(--destructive);
  --color-destructive-foreground: var(--destructive-foreground);
  --color-border: var(--border);
  --color-input: var(--input);
  --color-ring: var(--ring);
  --color-chart-1: var(--chart-1);
  --color-chart-2: var(--chart-2);
  --color-chart-3: var(--chart-3);
  --color-chart-4: var(--chart-4);
  --color-chart-5: var(--chart-5);
  --radius-sm: calc(var(--radius) - 4px);
  --radius-md: calc(var(--radius) - 2px);
  --radius-lg: var(--radius);
  --radius-xl: calc(var(--radius) + 4px);
  --color-sidebar: var(--sidebar);
  --color-sidebar-foreground: var(--sidebar-foreground);
  --color-sidebar-primary: var(--sidebar-primary);
  --color-sidebar-primary-foreground: var(--sidebar-primary-foreground);
  --color-sidebar-accent: var(--sidebar-accent);
  --color-sidebar-accent-foreground: var(--sidebar-accent-foreground);
  --color-sidebar-border: var(--sidebar-border);
  --color-sidebar-ring: var(--sidebar-ring);
  --font-size-4xl: 2.5rem;
}

.dark {
  --background: #000000;
  --foreground: #f3f3f3;
  --card: #000000;
  --card-foreground: #f3f3f3;
  --popover: #000000;
  --popover-foreground: #f3f3f3;
  --primary: #f3f3f3;
  --primary-foreground: #020202;
  --secondary: #040404;
  --secondary-foreground: #f3f3f3;
  --muted: #040404;
  --muted-foreground: #5a5a5a;
  --accent: #040404;
  --accent-foreground: #f3f3f3;
  --destructive: #380202;
  --destructive-foreground: #f50609;
  --border: #040404;
  --input: #040404;
  --ring: #151515;
  --chart-1: #0110c9;
  --chart-2: #008034;
  --chart-3: #fb5100;
  --chart-4: #6a0fff;
  --chart-5: #ff0317;
  --sidebar: #020202;
  --sidebar-foreground: #f3f3f3;
  --sidebar-primary: #0110c9;
  --sidebar-primary-foreground: #f3f3f3;
  --sidebar-accent: #040404;
  --sidebar-accent-foreground: #f3f3f3;
  --sidebar-border: #040404;
  --sidebar-ring: #151515;
}

