.section {
  padding: 6rem 1.5rem;
  background: linear-gradient(to bottom, var(--muted), var(--background));
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  text-align: center;
  margin-bottom: 4rem;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--foreground);
  margin-bottom: 1rem;
}

.subtitle {
  font-size: 1.125rem;
  color: var(--muted-foreground);
}

.stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
  padding: 2rem;
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 1rem;
}

.stat {
  text-align: center;
}

.statNumber {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--primary);
  margin-bottom: 0.5rem;
}

.statLabel {
  font-size: 0.875rem;
  color: var(--muted-foreground);
  font-weight: 500;
}

.testimonials {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.testimonial {
  position: relative;
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 1rem;
  padding: 2rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.testimonial:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.1);
}

.quoteIcon {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  color: var(--primary);
  opacity: 0.2;
}

.stars {
  display: flex;
  gap: 0.25rem;
  color: #fbbf24;
  margin-bottom: 1rem;
}

.testimonialText {
  font-size: 0.9375rem;
  color: var(--foreground);
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.author {
  border-top: 1px solid var(--border);
  padding-top: 1rem;
}

.authorName {
  font-size: 1rem;
  font-weight: 600;
  color: var(--foreground);
  margin-bottom: 0.25rem;
}

.authorBusiness {
  font-size: 0.875rem;
  color: var(--muted-foreground);
}

@media (max-width: 768px) {
  .section {
    padding: 4rem 1.5rem;
  }

  .title {
    font-size: 2rem;
  }

  .testimonials {
    grid-template-columns: 1fr;
  }

  .stats {
    grid-template-columns: repeat(2, 1fr);
  }
}
