html {
    /* background-color: var(--primary-blue); */

}

.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* max-width: 1024px; */
    margin: auto;
    min-height: 100vh;
    text-align: center;
    padding: 30px;
    background-color: var(--primary-blue);
}

.title {
    color: var(--light-blue);
    font-size: 42px;
    font-weight: bold;
}

.form {
    max-width: 400px;
    width: 90%;
    margin: auto;
    text-align: start;
    border: 1px solid var(--primary-blue);
    padding: 20px;
    border-radius: 30px;
    backdrop-filter: blur(10px);
    background: linear-gradient(135deg, var(--light-blue) 0%, var(--white) 100%);;
}

.nav {
    margin-bottom: 18px;
    display: flex;
}

.navIten {
    display: flex;
    cursor: pointer;
    align-items: center;
}

.progressContainer {
    max-width: 400px;
    width: 100%;
    height: 20px;
    display: flex;
    gap: 5px;
    margin: auto;
}

.form h2 {
    text-align: center;
    font-weight: bold;
    font-size: 26px;
}

.progressIten {
    background-color: #777777;
    width: 100%;
    height: 10px;
    border-radius: 10px;
}

.progressActive {
    background-color: var(--primary-blue);
}

.progressOn {
    background-color: var(--secondary-blue);
}


.span {
    margin: 18px;
    text-align: center;
}

.span a {
    color: var(--primary-blue);
}

.title img {
    height: 60px;
    scale: 2;
}