.section {
  padding: 6rem 1.5rem;
  background: var(--background);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  text-align: center;
  margin-bottom: 4rem;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--foreground);
  margin-bottom: 1rem;
}

.subtitle {
  font-size: 1.125rem;
  color: var(--muted-foreground);
}

.content {
  display: grid;
  grid-template-columns: 1fr;
  gap: 4rem;
}

.sectionTitle {
  font-size: 1.75rem;
  font-weight: 600;
  color: var(--foreground);
  margin-bottom: 2rem;
  text-align: center;
}

.timeline {
  position: relative;
  padding-left: 2rem;
}

.timeline::before {
  content: "";
  position: absolute;
  left: 0.75rem;
  top: 0;
  bottom: 0;
  width: 2px;
  background: var(--border);
}

.timelineItem {
  position: relative;
  display: flex;
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.timelineItem:last-child {
  margin-bottom: 0;
}

.timelineMarker {
  position: absolute;
  left: -2rem;
  width: 2rem;
  height: 2rem;
  background: var(--primary);
  color: var(--primary-foreground);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 0.875rem;
  z-index: 1;
}

.timelineContent {
  flex: 1;
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 0.75rem;
  padding: 1.5rem;
}

.timelineDay {
  display: inline-block;
  background: var(--primary);
  color: var(--primary-foreground);
  font-size: 0.75rem;
  font-weight: 600;
  padding: 0.25rem 0.75rem;
  border-radius: 0.25rem;
  margin-bottom: 0.75rem;
}

.timelineTitle {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--foreground);
  margin-bottom: 0.5rem;
}

.timelineDescription {
  font-size: 0.9375rem;
  color: var(--muted-foreground);
  line-height: 1.6;
  margin: 0;
}

.slaGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
}

.slaCard {
  display: flex;
  gap: 1rem;
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 0.75rem;
  padding: 1.5rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.slaCard:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
}


.note {
  background: var(--muted);
  border-left: 4px solid var(--primary);
  border-radius: 0.5rem;
  padding: 1.5rem;
  margin: 20px 0;
}

.noteText {
  font-size: 1rem;
  color: var(--foreground);
  line-height: 1.6;
  margin: 0;
}

.noteText strong {
  color: var(--primary);
  font-weight: 700;
}

.slaIcon {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  background: var(--primary);
  color: var(--primary-foreground);
  border-radius: 0.5rem;
}

.slaContent {
  flex: 1;
}

.slaTitle {
  font-size: 1rem;
  font-weight: 600;
  color: var(--foreground);
  margin-bottom: 0.5rem;
}

.slaDescription {
  font-size: 0.875rem;
  color: var(--muted-foreground);
  margin-bottom: 0.5rem;
}

.slaHighlight {
  display: inline-block;
  background: var(--secondary);
  color: var(--secondary-foreground);
  font-size: 0.75rem;
  font-weight: 600;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
}

@media (max-width: 768px) {
  .section {
    padding: 4rem 1.5rem;
  }

  .title {
    font-size: 2rem;
  }

  .timeline {
    padding-left: 1.5rem;
  }

  .slaGrid {
    grid-template-columns: 1fr;
  }
}
