/* Melhorando espaçamento e layout do hero */
.hero {
    min-height: 100vh;
    display: flex;
    align-items: center;
    background: linear-gradient(135deg, var(--light-blue) 0%, var(--white) 100%);
    position: relative;
    overflow: hidden;
    padding: 6rem var(--spacing-md) var(--spacing-3xl);
  }
  
  .heroBackground {
    position: absolute;
    inset: 0;
    background-image: radial-gradient(circle at 25% 25%, rgba(0, 51, 102, 0.1) 0%, transparent 50%),
      radial-gradient(circle at 75% 75%, rgba(0, 122, 204, 0.1) 0%, transparent 50%);
  }
  
  .floatingElements {
    position: absolute;
    inset: 0;
    pointer-events: none;
    z-index: 5;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0;
    position: relative;
    z-index: 10;
  }
  
  @media (min-width: 768px) {
    .container {
      padding: 0 var(--spacing-md);
    }
  }
  
  .content {
    display: grid;
    grid-template-columns: 1fr;
    gap: var(--spacing-3xl);
    align-items: center;
  }
  
  @media (min-width: 1024px) {
    .content {
      grid-template-columns: 1fr 1fr;
      gap: var(--spacing-4xl);
    }
  }
  
  .textContent {
    text-align: center;
  }
  
  @media (min-width: 1024px) {
    .textContent {
      text-align: left;
    }
  }
  
  .certification {
    display: inline-block;
    background: rgba(0, 122, 204, 0.1);
    border: 1px solid rgba(0, 122, 204, 0.3);
    border-radius: 2rem;
    padding: var(--spacing-sm) var(--spacing-md);
    margin-bottom: var(--spacing-lg);
  }
  
  .certificationText {
    font-size: 0.875rem;
    font-weight: 600;
    color: var(--primary-blue);
    font-family: var(--font-work-sans);
  }
  
  .title {
    font-size: 2.5rem;
    font-weight: 800;
    line-height: 1.1;
    margin-bottom: var(--spacing-lg);
    font-family: var(--font-work-sans);
    color: var(--dark-gray);
  }
  
  @media (min-width: 768px) {
    .title {
      font-size: 3.5rem;
    }
  }
  
  @media (min-width: 1024px) {
    .title {
      font-size: 4rem;
    }
  }
  
  .titleBrand {
    background: linear-gradient(135deg, var(--primary-blue), var(--secondary-blue));
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    display: block;
  }
  
  .titleSubtitle {
    font-size: 1.25rem;
    color: var(--medium-gray);
    font-weight: 600;
    margin-top: var(--spacing-xs);
  }
  
  @media (min-width: 768px) {
    .titleSubtitle {
      font-size: 1.5rem;
    }
  }
  
  .titleHighlight {
    background: linear-gradient(135deg, var(--primary-blue), var(--secondary-blue));
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    font-weight: 600;
  }
  
  .subtitle {
    font-size: 1.125rem;
    color: var(--medium-gray);
    margin-bottom: var(--spacing-2xl);
    line-height: 1.6;
    font-family: var(--font-open-sans);
  }
  
  @media (min-width: 768px) {
    .subtitle {
      font-size: 1.25rem;
    }
  }
  
  .ctaButtons {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-md);
    margin-bottom: var(--spacing-3xl);
  }
  
  @media (min-width: 640px) {
    .ctaButtons {
      flex-direction: row;
      justify-content: center;
      gap: var(--spacing-lg);
    }
  }
  
  @media (min-width: 1024px) {
    .ctaButtons {
      justify-content: flex-start;
    }
  }
  
  .primaryButton {
    background: linear-gradient(135deg, var(--primary-blue), var(--secondary-blue));
    color: var(--white);
    padding: var(--spacing-md) var(--spacing-xl);
    border-radius: var(--border-radius);
    border: none;
    font-weight: 600;
    font-size: 1.1rem;
    font-family: var(--font-work-sans);
    cursor: pointer;
    transition: all var(--transition-normal);
    text-decoration: none;
    display: inline-flex;
    align-items: center;
    gap: var(--spacing-sm);
    text-align: center;
    justify-content: center;
  }
  
  .primaryButton:hover {
    box-shadow: 0 10px 25px rgba(0, 51, 102, 0.3);
    transform: translateY(-2px);
  }
  
  .buttonIcon {
    width: 1.25rem;
    height: 1.25rem;
  }
  
  .secondaryButton {
    background: transparent;
    color: var(--primary-blue);
    padding: var(--spacing-md) var(--spacing-xl);
    border: 2px solid var(--primary-blue);
    border-radius: var(--border-radius);
    font-weight: 600;
    font-size: 1.1rem;
    font-family: var(--font-work-sans);
    cursor: pointer;
    transition: all var(--transition-normal);
    text-decoration: none;
    display: inline-block;
    text-align: center;
  }
  
  .secondaryButton:hover {
    background: var(--primary-blue);
    color: var(--white);
  }
  
  .stats {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: var(--spacing-lg);
  }
  
  @media (min-width: 640px) {
    .stats {
      grid-template-columns: repeat(4, 1fr);
      gap: var(--spacing-xl);
    }
  }
  
  .statItem {
    text-align: center;
    transition: all var(--transition-normal);
    padding: var(--spacing-md);
  }
  
  .statItemActive {
    transform: scale(1.05);
  }
  
  .statNumber {
    font-size: 1.75rem;
    font-weight: 800;
    color: var(--primary-blue);
    display: block;
    font-family: var(--font-work-sans);
    margin-bottom: var(--spacing-xs);
  }
  
  @media (min-width: 768px) {
    .statNumber {
      font-size: 2rem;
    }
  }
  
  .statLabel {
    font-size: 0.875rem;
    color: var(--medium-gray);
    text-transform: uppercase;
    letter-spacing: 0.05em;
    font-weight: 600;
    font-family: var(--font-open-sans);
  }
  
  .visualContent {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .expertiseCard {
    background: rgba(255, 255, 255, 0.9);
    border: 1px solid var(--light-gray);
    border-radius: var(--border-radius-lg);
    padding: var(--spacing-2xl);
    box-shadow: var(--shadow-lg);
    backdrop-filter: blur(10px);
    max-width: 400px;
    width: 100%;
  }
  
  .expertiseGrid {
    display: grid;
    gap: var(--spacing-lg);
  }
  
  .expertiseItem {
    display: flex;
    align-items: flex-start;
    gap: var(--spacing-md);
  }
  
  .expertiseIcon {
    width: 2rem;
    height: 2rem;
    color: var(--primary-blue);
    flex-shrink: 0;
    margin-top: 0.25rem;
  }
  
  .expertiseTitle {
    font-size: 1.125rem;
    font-weight: 600;
    color: var(--dark-gray);
    margin-bottom: var(--spacing-xs);
    font-family: var(--font-work-sans);
  }
  
  .expertiseDescription {
    font-size: 0.875rem;
    color: var(--medium-gray);
    line-height: 1.5;
    font-family: var(--font-open-sans);
  }
  
  .floatingElement {
    position: absolute;
    background: rgba(255, 255, 255, 0.9);
    border: 1px solid var(--light-gray);
    border-radius: var(--border-radius);
    padding: var(--spacing-md);
    box-shadow: var(--shadow-sm);
    animation: float 6s ease-in-out infinite;
    backdrop-filter: blur(10px);
    text-align: center;
    min-width: 140px;
  }
  
  .element1 {
    top: 10%;
    left: 5%;
    animation-delay: 0s;
  }
  
  .element2 {
    top: 20%;
    right: 10%;
    animation-delay: 2s;
  }
  
  .element3 {
    bottom: 30%;
    left: 10%;
    animation-delay: 4s;
  }
  
  .element4 {
    bottom: 10%;
    right: 5%;
    animation-delay: 1s;
  }
  
  .elementIcon {
    width: 1.5rem;
    height: 1.5rem;
    color: var(--primary-blue);
    margin-bottom: var(--spacing-xs);
  }
  
  .elementTitle {
    font-size: 0.875rem;
    font-weight: 600;
    color: var(--dark-gray);
    margin-bottom: var(--spacing-xs);
    font-family: var(--font-work-sans);
  }
  
  .elementDescription {
    font-size: 0.75rem;
    color: var(--medium-gray);
    font-family: var(--font-open-sans);
  }
  
  @keyframes float {
    0%,
    100% {
      transform: translateY(0px);
    }
    50% {
      transform: translateY(-20px);
    }
  }
  