.grid {
  display: grid;
  gap: 1.5rem;
  max-width: 72rem;
  margin: 0 auto;
  padding: 0 2rem;
}

@media (min-width: 768px) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  position: relative;
  background-color: var(--card);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  transition: transform 0.3s, box-shadow 0.3s;
}

.cardPopular {
  border: 2px solid var(--primary);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  transform: scale(1.05);
}

.badge {
  position: absolute;
  top: -1rem;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--primary);
  color: var(--primary-foreground);
  padding: 0.25rem 1rem;
  border-radius: 9999px;
  font-size: 0.875rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.header {
  text-align: center;
  padding: 2rem 1.5rem;
  border-bottom: 1px solid var(--border);
}

.planName {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.planDescription {
  font-size: 0.875rem;
  color: var(--muted-foreground);
  margin-bottom: 1rem;
}

.priceWrapper {
  display: flex;
  align-items: baseline;
  justify-content: center;
  gap: 0.25rem;
}

.price {
  font-size: 2.25rem;
  font-weight: 700;
}

.period {
  color: var(--muted-foreground);
}

.content {
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.features {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.feature {
  display: flex;
  align-items: flex-start;
  gap: 0.5rem;
}

.checkIcon {
  color: var(--primary);
  flex-shrink: 0;
  margin-top: 0.125rem;
}

.featureText {
  font-size: 0.875rem;
}

.button {
  width: 100%;
  height: 2.75rem;
  border-radius: calc(var(--radius) - 2px);
  font-size: 1rem;
  font-weight: 500;
  border: none;
  transition: opacity 0.2s;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.buttonPrimary {
  background-color: var(--primary);
  color: var(--primary-foreground);
}

.buttonPrimary:hover {
  opacity: 0.9;
}

.buttonOutline {
  background-color: transparent;
  color: var(--foreground);
  border: 1px solid var(--input);
}

.buttonOutline:hover {
  background-color: var(--accent);
  color: var(--accent-foreground);
}
