/* Melhorando espaçamento e responsividade do header */
.header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 50;
    transition: all var(--transition-normal);
  }
  
  .headerScrolled {
    background-color: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(8px);
    border-bottom: 1px solid var(--light-gray);
    box-shadow: var(--shadow-md);
  }
  
  .headerTransparent {
    background-color: transparent;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: var(--spacing-md) var(--spacing-lg);
  }
  
  @media (min-width: 768px) {
    .container {
      padding: var(--spacing-md) var(--spacing-xl);
    }
  }
  
  .nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 4.5rem;
    gap: var(--spacing-md);
  }
  
  .navLink {
    color: var(--dark-gray);
    text-decoration: none;
    font-weight: 500;
    font-family: var(--font-open-sans);
    transition: all var(--transition-normal);
    position: relative;
    padding: var(--spacing-xs) 0;
  }
  
  .navLink:hover {
    color: var(--primary-blue);
  }
  
  .navLink::after {
    content: "";
    position: absolute;
    width: 0;
    height: 2px;
    bottom: -4px;
    left: 0;
    background: linear-gradient(135deg, var(--primary-blue), var(--secondary-blue));
    transition: width var(--transition-normal);
  }
  
  .navLink:hover::after {
    width: 100%;
  }
  
  .ctaButton {
    background: linear-gradient(135deg, var(--primary-blue), var(--secondary-blue));
    color: var(--white);
    padding: var(--spacing-sm) var(--spacing-lg);
    border-radius: var(--border-radius);
    border: none;
    font-weight: 600;
    font-family: var(--font-work-sans);
    cursor: pointer;
    transition: all var(--transition-normal);
    font-size: 0.9rem;
  }
  
  .ctaButton:hover {
    box-shadow: 0 10px 25px rgba(0, 51, 102, 0.3);
    transform: translateY(-2px);
  }
  
  .mobileMenuButton {
    display: block;
    background: none;
    border: none;
    cursor: pointer;
    color: var(--primary-blue);
    transition: color var(--transition-normal);
    padding: var(--spacing-xs);
  }
  
  .mobileMenuButton:hover {
    color: var(--secondary-blue);
  }
  
.logoIcon {
  display: flex;
}

.logoIcon img {
  width: 60px;
}

  @media (min-width: 768px) {
    .mobileMenuButton {
      display: none;
    }
  }
  
  .mobileNav {
    display: block;
    margin-top: var(--spacing-md);
    padding: var(--spacing-lg);
    border-top: 1px solid var(--light-gray);
    animation: slideInUp var(--transition-normal) ease-out;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(8px);
    border-radius: var(--border-radius);
  }
  
  @media (min-width: 768px) {
    .mobileNav {
      display: none;
    }
  }
  
  .mobileNavContent {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-md);
  }
  
  .mobileNavLink {
    color: var(--dark-gray);
    text-decoration: none;
    font-weight: 500;
    font-family: var(--font-open-sans);
    transition: color var(--transition-normal);
    padding: var(--spacing-sm) 0;
  }
  
  .mobileNavLink:hover {
    color: var(--primary-blue);
  }
  
  .mobileCtaButton {
    width: 100%;
    background: linear-gradient(135deg, var(--primary-blue), var(--secondary-blue));
    color: var(--white);
    padding: var(--spacing-sm);
    border-radius: var(--border-radius);
    border: none;
    font-weight: 600;
    font-family: var(--font-work-sans);
    cursor: pointer;
    transition: all var(--transition-normal);
    margin-top: var(--spacing-xs);
  }
  
  .mobileCtaButton:hover {
    box-shadow: 0 8px 20px rgba(0, 51, 102, 0.3);
  }
  
  @keyframes techGlow {
    0% {
      filter: drop-shadow(0 0 5px rgba(0, 51, 102, 0.5));
    }
    100% {
      filter: drop-shadow(0 0 15px rgba(0, 122, 204, 0.8));
    }
  }
  
  @keyframes pulse {
    0%,
    100% {
      opacity: 1;
    }
    50% {
      opacity: 0.5;
    }
  }
  
  @keyframes slideInUp {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .logo {
    display: flex;
    align-items: center;
    gap: var(--spacing-xs);
    text-decoration: none;
    transition: transform var(--transition-normal);
  }
  
  .logo:hover {
    transform: scale(1.05);
  }
  
  .logoIcon {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .logoIconMain {
    height: 2rem;
    width: 2rem;
    color: var(--primary-blue);
    animation: techGlow 2s ease-in-out infinite alternate;
  }
  
  .logoIconSecondary {
    height: 1rem;
    width: 1rem;
    color: var(--secondary-blue);
    position: absolute;
    bottom: -0.25rem;
    right: -0.25rem;
    animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  }
  
  .logoText {
    font-size: 1.5rem;
    font-weight: 700;
    font-family: var(--font-work-sans);
    background: linear-gradient(135deg, var(--primary-blue), var(--secondary-blue));
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
  }
  
  .desktopNav {
    display: none;
    align-items: center;
    gap: var(--spacing-lg);
  }
  
  @media (min-width: 768px) {
    .desktopNav {
      display: flex;
    }
  }
  