.footer {
    background: #003366;
    border-top: 1px solid var(--color-border);
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: var(--spacing-3xl) var(--spacing-md) 0px var(--spacing-md); 
  }
  
  .footerGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: var(--spacing-xl);
  }
  
  .brand {
    display: flex;
    align-items: center;
    gap: var(--spacing-xs);
    margin-bottom: var(--spacing-md);
  }
  
  .logoContainer {
    position: relative;
    display: flex;
  }

  .logoContainer img {
    height: 40px;
    scale: 1.8;
  }
  
  .logoMain {
    height: 2rem;
    width: 2rem;
    color: var(--color-primary);
  }
  
  .logoSecondary {
    height: 1rem;
    width: 1rem;
    color: var(--color-secondary);
    position: absolute;
    bottom: -4px;
    right: -4px;
  }
  
  .brandName {
    font-size: var(--font-size-2xl);
    font-weight: var(--font-weight-bold);
    font-family: var(--font-sans);
    background: linear-gradient(to right, var(--color-primary), var(--color-secondary));
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  .brandDescription {
    color: #aaa;
    margin-bottom: var(--spacing-md);
    font-family: var(--font-serif);
  }
  
  .sectionTitle {
    font-weight: var(--font-weight-semibold);
    color: #fff;
    margin-bottom: var(--spacing-md);
    font-family: var(--font-sans);
  }
  
  .linkList {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .linkList li {
    margin-bottom: var(--spacing-xs);
  }
  
  .link {
    color: #aaa;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .link:hover {
    color: var(--color-primary);
  }
  
  .contactList {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-sm);
  }
  
  .contactItem {
    display: flex;
    align-items: center;
    color: #aaa;
  }
  
  .contactIcon {
    height: 1rem;
    width: 1rem;
    margin-right: var(--spacing-xs);
    color: #aaa;
  }
  
  .contactText {
    font-size: var(--font-size-sm);
  }
  
  .footerBottom {
    border-top: 1px solid var(--color-border);
    margin-top: var(--spacing-xl);
    padding: 10px;
    text-align: center;
    color: #eee;
  }
  
  @media (max-width: 768px) {
    .footerGrid {
      grid-template-columns: 1fr;
      gap: var(--spacing-lg);
    }
  }
  