.section {
  padding: 6rem 1.5rem;
  background: var(--background);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  text-align: center;
  margin-bottom: 4rem;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--foreground);
  margin-bottom: 1rem;
}

.subtitle {
  font-size: 1.125rem;
  color: var(--muted-foreground);
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.6;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.card {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 1rem;
  padding: 2rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.1);
}

.iconWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3.5rem;
  height: 3.5rem;
  background: var(--primary);
  color: var(--primary-foreground);
  border-radius: 0.75rem;
  margin-bottom: 1.5rem;
}

.cardTitle {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--foreground);
  margin-bottom: 0.75rem;
}

.cardDescription {
  font-size: 0.9375rem;
  color: var(--muted-foreground);
  line-height: 1.6;
}

.highlight {
  background: linear-gradient(135deg, var(--primary) 0%, var(--secondary) 100%);
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
}

.highlightText {
  font-size: 1.125rem;
  color: var(--primary-foreground);
  line-height: 1.6;
  margin: 0;
}

.highlightText strong {
  font-weight: 700;
}

@media (max-width: 768px) {
  .section {
    padding: 4rem 1.5rem;
  }

  .title {
    font-size: 2rem;
  }

  .grid {
    grid-template-columns: 1fr;
  }
}
