.section {
  padding: 6rem 1.5rem;
  background: linear-gradient(135deg, var(--primary) 0%, var(--secondary) 100%);
  position: relative;
  overflow: hidden;
}

.section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23ffffff' fill-opacity='0.05'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  opacity: 0.3;
}

.container {
  max-width: 900px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.content {
  text-align: center;
  margin-bottom: 3rem;
}

.badge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  color: var(--primary-foreground);
  padding: 0.5rem 1rem;
  border-radius: 2rem;
  font-size: 0.875rem;
  font-weight: 600;
  margin-bottom: 2rem;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
}

.title {
  font-size: 3rem;
  font-weight: 700;
  color: var(--primary-foreground);
  margin-bottom: 1.5rem;
  line-height: 1.2;
}

.description {
  font-size: 1.125rem;
  color: var(--primary-foreground);
  line-height: 1.6;
  margin-bottom: 2rem;
  opacity: 0.95;
}

.description strong {
  font-weight: 700;
  text-decoration: underline;
}

.features {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  flex-wrap: wrap;
  margin-bottom: 2.5rem;
}

.feature {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--primary-foreground);
  font-size: 1rem;
  font-weight: 500;
}

.ctaButton {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  background: var(--primary-foreground);
  color: var(--primary);
  padding: 1rem 2.5rem;
  border-radius: 0.75rem;
  font-size: 1.125rem;
  font-weight: 600;
  text-decoration: none;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
}

.ctaButton:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.3);
}

.note {
  font-size: 0.875rem;
  color: var(--primary-foreground);
  margin-top: 1.5rem;
  opacity: 0.9;
}

.urgency {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 2rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 1rem;
  padding: 2rem;
}

.urgencyItem {
  text-align: center;
}

.urgencyNumber {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--primary-foreground);
  margin-bottom: 0.5rem;
}

.urgencyLabel {
  font-size: 0.875rem;
  color: var(--primary-foreground);
  opacity: 0.9;
  font-weight: 500;
}

@media (max-width: 768px) {
  .section {
    padding: 4rem 1.5rem;
  }

  .title {
    font-size: 2rem;
  }

  .features {
    flex-direction: column;
    gap: 1rem;
  }

  .urgency {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
}
