.card {
  background-color: var(--card);
  border: 2px solid color-mix(in oklch, var(--primary) 20%, transparent);
  border-radius: var(--radius);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.header {
  text-align: center;
  padding: 1.5rem 1.5rem 0;
}

.title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

@media (min-width: 768px) {
  .title {
    font-size: 1.875rem;
  }
}

.description {
  color: var(--muted-foreground);
  font-size: 1rem;
}

.content {
  padding: 1.5rem;
}

.successContent {
  padding: 3rem 1.5rem;
  text-align: center;
}

.successIcon {
  width: 4rem;
  height: 4rem;
  background-color: color-mix(in oklch, var(--primary) 10%, transparent);
  border-radius: 9999px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 1rem;
  color: var(--primary);
}

.successTitle {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.successMessage {
  color: var(--muted-foreground);
  margin-bottom: 1.5rem;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.row {
  display: grid;
  gap: 1rem;
}

@media (min-width: 768px) {
  .row {
    grid-template-columns: repeat(2, 1fr);
  }
}

.field {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.label {
  font-size: 0.875rem;
  font-weight: 500;
}

.input {
  height: 2.75rem;
  padding: 0 0.75rem;
  border: 1px solid var(--input);
  border-radius: calc(var(--radius) - 2px);
  background-color: var(--background);
  color: var(--foreground);
  font-size: 0.875rem;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.input:focus {
  outline: none;
  border-color: var(--ring);
  box-shadow: 0 0 0 3px color-mix(in oklch, var(--ring) 20%, transparent);
}

.textarea {
  padding: 0.75rem;
  border: 1px solid var(--input);
  border-radius: calc(var(--radius) - 2px);
  background-color: var(--background);
  color: var(--foreground);
  font-size: 0.875rem;
  resize: none;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.textarea:focus {
  outline: none;
  border-color: var(--ring);
  box-shadow: 0 0 0 3px color-mix(in oklch, var(--ring) 20%, transparent);
}

.button {
  width: 100%;
  height: 3rem;
  background-color: var(--primary);
  color: var(--primary-foreground);
  border: none;
  border-radius: calc(var(--radius) - 2px);
  font-size: 1rem;
  font-weight: 500;
  transition: opacity 0.2s;
}

.button:hover:not(:disabled) {
  opacity: 0.9;
}

.button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.buttonOutline {
  background-color: transparent;
  color: var(--foreground);
  border: 1px solid var(--input);
}

.buttonOutline:hover {
  background-color: var(--accent);
  color: var(--accent-foreground);
}

.disclaimer {
  font-size: 0.75rem;
  text-align: center;
  color: var(--muted-foreground);
}
