.section {
  padding: 6rem 1.5rem;
  background: var(--background);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  text-align: center;
  margin-bottom: 4rem;
}

.title {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--foreground);
  margin-bottom: 1rem;
}

.subtitle {
  font-size: 1.125rem;
  color: var(--muted-foreground);
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
}

.card {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.1);
}

.iconWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  margin: 0 auto 1.5rem;
  background: var(--primary);
  color: var(--primary-foreground);
  border-radius: 1rem;
}

.cardTitle {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--foreground);
  margin-bottom: 0.75rem;
}

.cardDescription {
  font-size: 0.9375rem;
  color: var(--muted-foreground);
  line-height: 1.6;
}

.certifications {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  flex-wrap: wrap;
  padding: 2rem;
  background: var(--muted);
  border-radius: 1rem;
}

.certItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.75rem;
}

.certBadge {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  background: var(--primary);
  color: var(--primary-foreground);
  font-weight: 700;
  font-size: 0.875rem;
  border-radius: 50%;
}

.certText {
  font-size: 0.875rem;
  color: var(--muted-foreground);
  font-weight: 500;
}

@media (max-width: 768px) {
  .section {
    padding: 4rem 1.5rem;
  }

  .title {
    font-size: 2rem;
  }

  .grid {
    grid-template-columns: 1fr;
  }

  .certifications {
    gap: 2rem;
  }
}
