.hero {
  background: linear-gradient(135deg, #003366 0%, #007acc 100%);
  color: white;
  padding: 120px 20px 80px;
  text-align: center;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.content {
  max-width: 800px;
  margin: 0 auto;
}

.title {
  font-size: 3.5rem;
  font-weight: 800;
  line-height: 1.2;
  margin-bottom: 24px;
  color: #00D1C1;
}

.highlight {
  color: #FF8C42;
}

.subtitle {
  font-size: 1.5rem;
  line-height: 1.6;
  margin-bottom: 40px;
  opacity: 0.95;
}

.cta {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}

.primaryButton {
  background: #FF8C42;
  color: #1f2937;
  padding: 18px 48px;
  border-radius: 12px;
  font-size: 1.25rem;
  font-weight: 700;
  text-decoration: none;
  transition: transform 0.2s, box-shadow 0.2s;
  box-shadow: 0 10px 30px #FF8C42bb;
}

.primaryButton:hover {
  transform: translateY(-2px);
  box-shadow: 0 15px 40px #FF8C42cc;
}

.note {
  font-size: 0.95rem;
  opacity: 0.9;
}

@media (max-width: 768px) {
  .hero {
    padding: 80px 20px 60px;
  }

  .title {
    font-size: 2.5rem;
  }

  .subtitle {
    font-size: 1.25rem;
  }

  .primaryButton {
    padding: 16px 32px;
    font-size: 1.1rem;
  }
}
