.form {
    display: flex;
    flex-direction: column;
    animation: start .2s ease forwards;
}

.ask {
    font-size: 20px;
    margin-bottom: 6px;
}

.input {
    /* margin: 10px 0; */
    border: 1px solid var(--primary-blue);
    padding: 15px;
    outline: none;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.btn {
    background-color: var(--primary-blue);
    outline: none;
    color: #fff;
    /* font-size: 1.1rem; */
    font-weight: 800;
    border-radius: 0;
    border-end-start-radius: 10px;
    border-end-end-radius: 10px;
    overflow: hidden;
    border: 1px solid var(--primary-blue);
    border-top: none;
}

.btn:hover {
    font-size: 16px;
}

.btn:hover .input {
    background: var(--primary-blue);
}

.radio {
    display: flex;
    justify-content: space-evenly;
    margin: 1rem;
}

.radio div {
    display: flex;
    gap: 5px;
    align-items: center;
    font-size: 20px;
}

@keyframes start {
    0% {
        transform: translateX(30%);
        opacity: 0;

    }

    50% {
        opacity: 0;
    }

    100% {
        transform: translateX(0%);
        opacity: 1;
    }
}