.container {
    margin-top: 8rem;
    margin-bottom: 2rem;
}

.servicesGrid {
    margin-top: 2rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: var(--spacing-xl);
  }
  
  .card {
    background: var(--color-card);
    border: 1px solid var(--color-border);
    border-radius: var(--border-radius-lg);
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* padding: 1.5rem; */
    overflow: hidden;
  }
  
  .infos {
    padding: 1.5rem;
  }

  .serviceCard {
    padding: var(--spacing-xl);
    animation: slideInUp 0.8s ease-out;
  }
  
  .serviceCard:hover {
    box-shadow: var(--shadow-xl);
    transform: translateY(-8px);
  }
  
  .icon {
    background-color: #bbb;
    display: inline-flex;
    padding: 1rem;
    border-radius: 50%;
    margin-bottom: 15px;
  }

  .price {
    font-weight: 500;
    color: var(--primary-blue);
    font-size: 20px;
    margin: 15px 0;
  }

  .button {
    width: 100%;
    color: var(--primary-blue);
    border-radius: 0;
    font-size: 18px;
    transition: all .3s;
  }

  .button:hover {
    background-color: #00000033;
    font-size: 20px;
}

.card:hover {
    transform: translateY(-10px);
}

.desc {
    font-size: 1.3rem;
    font-family: serif;
    max-width: 900px;
    max-width: 90%;
    margin: 2rem auto;
}

.iten {
    width: 6px;
    height: 6px;
    background-color: var(--primary-blue);
    border-radius: 50%;
    margin-right: 5px;
}

.feature {
    display: flex;
    align-items: center;
    
}