.page {
  min-height: 100vh;
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-blue) 100%);
  padding: 40px 20px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.backLink {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  color: white;
  text-decoration: none;
  font-size: 0.95rem;
  margin-bottom: 32px;
  transition: opacity 0.2s;
}

.backLink:hover {
  opacity: 0.8;
}

.content {
  background: white;
  border-radius: 16px;
  padding: 48px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);
}

.header {
  text-align: center;
  margin-bottom: 48px;
}

.title {
  font-size: 2.5rem;
  font-weight: 800;
  color: #1f2937;
  margin-bottom: 16px;
  line-height: 1.2;
}

.subtitle {
  font-size: 1.125rem;
  color: #6b7280;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.layout {
  display: grid;
  gap: 48px;
  grid-template-columns: 1fr;
}

@media (min-width: 1024px) {
  .layout {
    grid-template-columns: 1fr 400px;
  }
}

.formSection {
  /* Form styles handled by LeadForm component */
}

.infoSection {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.infoCard {
  background: #f9fafb;
  border-radius: 12px;
  padding: 32px;
  border: 1px solid #e5e7eb;
}

.infoTitle {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin-bottom: 24px;
}

.steps {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.step {
  display: flex;
  gap: 16px;
  align-items: flex-start;
}

.stepNumber {
  width: 32px;
  height: 32px;
  background: linear-gradient(135deg, var(--primary), var(--primary-blue));
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 0.875rem;
  flex-shrink: 0;
}

.stepContent h4 {
  font-size: 1rem;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 4px;
}

.stepContent p {
  font-size: 0.875rem;
  color: #6b7280;
  line-height: 1.5;
}

.guarantees {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.guarantee {
  display: flex;
  gap: 12px;
  align-items: flex-start;
  padding: 16px;
  background: white;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
}

.guarantee svg {
  color: #667eea;
  flex-shrink: 0;
  margin-top: 2px;
}

.guarantee h4 {
  font-size: 0.95rem;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 2px;
}

.guarantee p {
  font-size: 0.875rem;
  color: #6b7280;
}

@media (max-width: 768px) {
  .content {
    padding: 32px 24px;
  }

  .title {
    font-size: 2rem;
  }

  .subtitle {
    font-size: 1rem;
  }

  .infoCard {
    padding: 24px;
  }
}
