.section {
    background-color: #fff;
    padding: 30px;
}

.cardConteainer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
}

.card {
    border-radius: 30px;
    border: 1px solid #777;
    overflow: hidden;
    position: relative;
    transition: all .3s;

}

.img {
    width: 100%;
    min-height: 300px;
    height: 300px;
    object-fit: cover;
    overflow: hidden;
    transition: all .3s;
    max-height: 300px;

}

.infosContainer1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0 10px;
    font-size: 13px;
    margin-bottom: 15px;
}

.category {
    padding: 3px 10px;
    background-color: #ccc;
    color: #000;
    border-radius: 16px;

}

.title {
    padding: 0 10px;
}

.desc {
    padding: 0 10px;
    font-weight: 300;
}

.techConteiner {
    margin-bottom: 10px;
    padding: 0 10px;
}

.techConteiner h4 {
    margin-bottom: 10px;
}

.techs {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    margin-bottom: 5px;
}

.techs span {
    background-color: #ccc;
    color: #000;
    border-radius: 5px;
    padding: 1px 5px;
    font-size: 12px;
}

.features {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.features div {
    font-size: 12px;
    display: flex;
    align-items: center;
    gap: 5px;
}

.featureBall {
    width: 3px;
    height: 3px;
    background-color: var(--primary-blue);
    border-radius: 50%;
}

.external {
    position: absolute;
    top: 10px;
    right: 10px;
    opacity: 0.6;
    transition: all .3s;
}

.card:hover .external{
    opacity: 1;
}

.card:hover .img {
    /* transform: scale(1.3); */
    scale: 1.01;
}

.card:hover {
    border: 1px solid #000;
}

@media (max-width: 800px) {
    .cardConteainer {
        grid-template-columns: 1fr;
    }
}